import React from 'react';

const Value = (props) => {
  return (
    <>
      <tr className = 'active'>
        <td className = 'purple'>{props.state}</td>
        <td className = 'good'>{props.confirmed}</td>
        <td className = 'good'>{props.recovered}</td>
        <td className = 'death'>{props.deaths}</td>
        <td className = 'orange'>{props.active}</td>
        <td className = 'purple'>{props.lastupdatedtime}</td>
      </tr>
    </>
  );
}

export default Value;