import React from 'react';
import DataHead from './DataHead';

const App = () => {

    return (
        <>
        <div id = 'container'>
            <h1 id = 'header'>India Covid-19 Dashboard</h1>
            <div className = 'dataCustom'>
                <span id = 'stt'>State</span>
                <span id = 'conf'>Confirmed</span>
                <span id = 'rec'>Recovered</span>
                <span id = 'dea'>Deaths</span>
                <span id = 'act'>Active</span>
                <span id = 'upd'>Updated</span>
            </div>
            <div id = 'data'>
                <DataHead/>
            </div>
            <footer style = {{marginTop : '10px'}}>Made with ❤️ by Avinash Gupta</footer>
        </div>
        </>
    );

}

export default App;