import React, { useEffect , useState } from 'react';
import Value from './Value';

const DataHead = () => {

  const [dataArray , setDataArray] = useState([]);
  
  const getDataFromAPI = async () => {
    const response = await fetch('https://api.covid19india.org/data.json');
    const data = await response.json();
    setDataArray(data.statewise);
    console.log(dataArray);
  }

  useEffect(() => {
    getDataFromAPI();
  } , [])

  return (
    <>
    <table className = "table  mb-0 table-bordered">
      <tbody>
      {
        dataArray.map((currState) => {
          return <Value
                  state = {currState.state}
                  confirmed = {currState.confirmed}
                  recovered = {currState.recovered}
                  deaths = {currState.deaths}
                  active = {currState.active}
                  lastupdatedtime = {currState.lastupdatedtime}/>
        })
      }
      </tbody>
    </table>
    </>
  );
}

export default DataHead;